@font-face {
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  src: local("Lato Regular"), local("Lato-Regular"), url("../fonts/lato-regular.woff2") format("woff2"), url("../fonts/lato-regular.woff") format("woff");
}

@font-face {
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-style: italic;
  font-weight: 400;
  src: local("Lato Italic"), local("Lato-Italic"), url("../fonts/lato-italic.woff2") format("woff2"), url("../fonts/lato-italic.woff") format("woff");
}

@font-face {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}

@font-face {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-style: italic;
  font-weight: 700;
  src: local("Montserrat Bold Italic"), local("Montserrat-BoldItalic"), url("../fonts/montserrat-bolditalic.woff2") format("woff2"), url("../fonts/montserrat-bolditalic.woff") format("woff");
}

@font-face {
  font-family: "Arbutus Slab", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

input::-ms-clear {
  display: none;
}

a {
  text-decoration: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 2.65em;
  height: 1em;
  fill: #3F566E;
}

body {
  min-width: 320px;
  color: #3F566E;
  background: #fff;
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.43;
}

body.is-overflow-hidden {
  overflow: hidden;
}

.out {
  overflow: hidden;
}

.inner {
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.block {
  display: flex;
}

.header {
  padding-top: 47px;
}

@media only screen and (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.header__nav {
  position: relative;
  z-index: 1000;
  float: right;
  margin-right: -7px;
  width: 31px;
  height: 31px;
  background: none;
}

.header__nav.is-active:before {
  transform: translate3d(0px, -3px, 0) rotate(-45deg) scaleX(1.4);
}

.header__nav.is-active:after {
  transform: translate3d(0px, 2px, 0) rotate(45deg) scaleX(1.4);
}

.header__nav:before, .header__nav:after {
  content: '';
  position: absolute;
  right: 7px;
  width: 17px;
  height: 3px;
  background: #3F566E;
  transform-origin: right center;
  transition: transform 0.25s;
}

.header__nav:before {
  top: 8px;
}

.header__nav:after {
  bottom: 8px;
}

.logo {
  position: relative;
  z-index: 1000;
  display: inline-block;
  vertical-align: top;
  font-size: 31px;
}

.logo__icon {
  display: block;
  fill: #3F566E;
}

.nav {
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .nav {
    text-align: left;
  }
}

.nav__item:not(:last-child) {
  margin-bottom: 5px;
}

.nav__link {
  display: block;
  color: #3F566E;
  font-family: "Arbutus Slab", Helvetica, Arial, sans-serif;
  font-size: 70px;
  font-weight: 400;
  line-height: 76px;
  text-transform: lowercase;
  transition: color 0.25s;
}

@media only screen and (max-width: 1023px) {
  .nav__link {
    font-size: 60px;
    line-height: 1.1;
  }
}

@media only screen and (max-width: 767px) {
  .nav__link {
    font-size: 50px;
  }
}

.no-touch .nav__link:hover {
  color: #FFDC45;
}

.nav__link.is-active {
  color: #FFDC45;
}

.menu {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-top: 100px;
  min-width: 320px;
  background: rgba(250, 250, 250, 0.9);
  box-shadow: 0 24px 55px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  font-size: 0;
  transform: translate3d(0, -110%, 0);
  transition: transform 0.3s ease-in-out;
}

.menu:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@supports (backdrop-filter: blur(11px)) {
  .menu {
    background: rgba(250, 250, 250, 0.69);
    backdrop-filter: blur(11px);
  }
}

@media only screen and (max-width: 767px) {
  .menu {
    padding-top: 71px;
  }
}

.menu.is-open {
  transform: translate3d(0, 0, 0);
}

.menu__center {
  display: inline-block;
  vertical-align: middle;
  overflow: auto;
  padding-bottom: 78px;
  width: 100%;
  max-height: 100%;
}

@media only screen and (max-width: 767px) {
  .menu__center {
    padding-bottom: 20px;
  }
}

.menu__content {
  position: relative;
  float: left;
  width: 50%;
  text-align: left;
}

@media only screen and (max-width: 1023px) {
  .menu__content {
    width: 60%;
  }
}

@media only screen and (max-width: 767px) {
  .menu__content {
    width: 100%;
  }
}

.menu__nav {
  float: right;
  width: 50%;
}

@media only screen and (max-width: 1023px) {
  .menu__nav {
    width: 40%;
  }
}

@media only screen and (max-width: 767px) {
  .menu__nav {
    padding-bottom: 50px;
    width: 100%;
  }
}

.menu__info {
  padding-bottom: 22px;
}

.menu__icon {
  margin-bottom: 7px;
}

.menu__subtitle {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.4;
  letter-spacing: 0.4px;
}

.menu__meta {
  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.02px;
}

.menu__meta a {
  color: inherit;
  transition: color 0.25s;
}

.no-touch .menu__meta a:hover {
  color: #FFDC45;
}

.menu__subnav {
  margin: 69px -13px 0;
}

@media only screen and (max-width: 1023px) {
  .menu__subnav {
    margin-top: 13px;
  }
}

.menu__item {
  display: inline-block;
  vertical-align: top;
}

@media only screen and (max-width: 767px) {
  .menu__item {
    display: block;
  }
}

.menu__link {
  display: inline-block;
  vertical-align: top;
  padding: 0 13px;
  color: #3F566E;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 46px;
  letter-spacing: 0.34px;
  transition: color 0.25s;
}

.no-touch .menu__link:hover {
  color: #FF539E;
}

.menu__link.is-active {
  color: #FF539E;
}
