.nav {
	text-align: right;
	@include sm {
		text-align: left; }
	&__item {
		&:not(:last-child) {
			margin-bottom: 5px; } }
	&__link {
		display: block;
		color: $dark;
		font-family: $arbutus;
		@include f(70px, 400, 76px);
		text-transform: lowercase;
		transition: color 0.25s;
		@include md {
			font-size: 60px;
			line-height: 1.1; }
		@include sm {
			font-size: 50px; }
		@include hover {
			color: $yellow; }
		&.is-active {
			color: $yellow; } } }
